:root {
    --color-bg: rgb(0, 125, 92);
    --color-black: rgb(0, 0, 0);
    --color-white: #fff;
    --color-light: rgb(241, 241, 241);
    --color-variant: rgb(0, 137, 123);
    --color-btn: rgb(216, 27, 96);
    --color-bg-variant: rgb(146, 146, 146);

    --transition: all 400ms ease;
    width: 1365px;
    max-resolution: 100%;
    background: linear-gradient(to right, rgb(1, 13, 135), rgb(203, 132, 158));
    font-family: Verdana, Geneva, Tahoma, sans-serif
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: 2vh;
}

.body {
    font-family: sans-serif, 'sans-serif';
    color: #000;
 }

 a:link {
    text-decoration-line: none;
    color: var(--color-black);
    cursor: pointer;
 }

 /* a:hover {
        text-box: rgb(106, 106, 106);
 } */

/* ========================== MEDIA QUERIES (MEDIUM DEVICES) ====================== */
@media screen and (max-width: 1024px) {
    :root{
        width: 1024px;
    }

}

/* ========================== MEDIA QUERIES (SMALL DEVICES) ====================== */
@media screen and (max-width: 768px) {
    :root{
        width: 400px;
        height: auto;
    }
 
}